<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <Table :columns="columns" :data="tableData">
        <template slot-scope="{ row }" slot="2">
          <span style="color:#538FFF">{{row['2']}}</span>
        </template>
        <template slot-scope="{row}" slot="action">
          <div>
            <button @click="handleSure(row)">通过</button>
            <button @click="handleBack(row)">驳回</button>
            <button @click="handleLook(row)">查看申请</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage"
        :total="total"
        :page-size="pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <!-- 驳回原因 -->
    <Modal v-model="backDilog" :footer-hide="true" title="驳回" ok-text="驳回">
      <Form ref="backFormValidate" :model="backForm" :rules="backFormRuleValidate" :label-width="0">
        <div style="margin-bottom:10px">请输入驳回原因：</div>
        <FormItem label prop="backInput">
          <Input
            v-model="backForm.backInput"
            :maxlength="100"
            show-word-limit
            type="textarea"
            placeholder="请输入原因"
            style="width:100%"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="backDilog=false">取消</Button>
        <Button type="primary" @click="backSubmit">确定</Button>
      </div>
    </Modal>

    <!-- 案件中止/恢复 -->
    <Modal v-model="stopDialog" :footer-hide="true" :title="isStop?'案件中止':'案件恢复'">
      <Form ref="stopFormValidate" :model="stopForm"  :label-width="110">
        <FormItem label="起始日期" v-if="isStop">
          <DatePicker disabled type="date" placeholder v-model="stopForm.startTime"></DatePicker>
        </FormItem>
        <FormItem label="结束日期" v-else>
          <DatePicker disabled  type="date" placeholder v-model="stopForm.endTime"></DatePicker>
        </FormItem>
        <FormItem label="延期事由">
          <Select disabled v-model="stopForm.reason">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="材料附件">
            <a style="color:#57a3f3">{{'未选择文件'}}</a>
        </FormItem>
        <FormItem label="备注信息">
          <Input
            v-model="stopForm.remarks"
            disabled
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="请输入备注信息"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button type="primary" @click="stopDialog=false">关闭</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
// 中止恢复审核
import listSearch from "@/components/list-search/search.vue";
const columns = [
  { title: "案号", slot: "2" },
  { title: "申请人" },
  { title: "代理人" },
  { title: "被申请人" },
  { title: "中止时间" },
  {title:"恢复时间"},
  {title:"案件进度"},
  { title: "案件类型" },
  { title: "案件状态" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
    components:{
        listSearch
    },
  data() {
    return {
      searchData: {
        number: "",
        appeal: "",
        backAppeal: "",
        progress: [],
        regName:[],
        caseType: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backAppeal",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "progress",
          label: "案件进度",
          placeholder: "请选择案件进度",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
             type: "select",
          value: "regName",
          label: "注册名称",
          placeholder: "请选择注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      currentPage:1,
      total:0,
      pageSize:10,
      columns:columns,
      tableData:[{1:1,2:2}],
      backDilog:false, // 驳回
      stopDialog:false, // 案件中止弹窗
      isStop:false, // 案件中止||恢复
      // 驳回
      backForm: { backInput: "" },
      backFormRuleValidate: {
        backInput: [
          { required: true, message: "请输入原因", trigger: "change" }
        ]
      },
      stopForm:{
          startTime:"",
          endTime:"",
          reason:"",
          file:"",
          remarks:""
      }
      
    };
  },
  methods: {
    goSearch() {},
     //   同意通过
    handleSure(row){
        this.$Modal.confirm({
          title: "操作",
          content:
            '<div style="color:#3B4453;text-align:center;font-size:18px">确认通过中止/恢复申请？</div>',
          onOk: () => {
            this.$Message.success("操作成功");
            this.$Message.error("系统出错，请稍后重试");
          },
          onCancel: () => {}
        });
    },
      //   驳回提交
    backSubmit() {
      this.$refs["backFormValidate"].validate(valid => {
        console.log(this.backForm);
        if (valid) {
          this.$Message.success("操作成功");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    // 驳回
    handleBack(row){
        this.backDilog=true
    },
    // 查看
    handleLook(row){
        
        this.isStop=true; // 案件中止 / 恢复
        this.stopDialog=true
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/.ivu-date-picker {
  width: 100%;
}
</style>
